import request from '@/utils/request'

/**
 * 
 */
export function getUserInviteInfo() {
  return request({
    url: '/api/User/getUserInviteInfo',
    method: 'post'
  })
}

/**
 * 
 */
export function getUserInviteList(data) {
  return request({
    headers: {
      version: 'v100'
    },
    url: '/api/User/getUserInviteList',
    method: 'post',
    data
  })
}

